import React from 'react'
import BungieApi from "../helpers/BungieApi";
import EntryItem from "./EntryItem";
import PageTitle from './PageTitle';

export default function Lore(props) {
    const bungieApi = new BungieApi();

    const newProps = {...props, dataPath: "DestinyLoreDefinition"};
    return (
        <EntryItem {...newProps}>
            {data => (
                <div>
                    <PageTitle text={data.displayProperties.name}/>
                    <div className="section content subtitle is-2">
                        {data.subtitle}
                    </div>
                    <div className="section content"
                        dangerouslySetInnerHTML={bungieApi.makeHtml(bungieApi.formatPlainText(data.displayProperties.description))}>
                    </div>
                </div>
            )
            }
        </EntryItem>);
}