import React from 'react'
import EntryItem from "./EntryItem";

export default function ActivityModeSummary(props) {

    const activityProps = { ...props, dataPath: "DestinyActivityModeDefinition"};

    return (
       <EntryItem {...activityProps}>
           {(data) => (
               <div>{data.displayProperties.name}</div>
           )}
       </EntryItem>
    )
}