import React, { useState, useEffect } from 'react'
import axios from "axios";
import BungieApi from "../helpers/BungieApi";

export default function EntryItem(props) {
    const bungieApi = new BungieApi();

    const defaultData = {
        details : {},
        loading : true
    };

    const [itemDetails, setItemDetails] = useState(defaultData);

    useEffect(() => {
        let dataPath = bungieApi.getDataUrl(props.dataPath);
        const getData = async () => {
            const itemPath = props.hash || props.match.params.id;
            const {data} = await axios(`${dataPath}/${itemPath}.json`);

            // Update state
            setItemDetails( {details: data, loading: false})
        };

        getData();
    }, [props]);

    const makeHtml = function(text) {
        return {__html: text}
    };

    return  itemDetails.loading ? (
        <div>Loading...</div>
    ) : (
        <div>
            {
                props.children(itemDetails.details)
            }
        </div>
    );
}