import React from 'react'
import BungieApi from "../helpers/BungieApi";


export default function MediaCard(props) {
    const bungieApi = new BungieApi();

return (
    <div className="media section">
        <div className="media-left">
            {
                props.hasIcon &&
                <figure className={"icon-image image is-96x96"}>
                    <img src={bungieApi.getBungieImagePath(props.icon)} alt="Item image"/>
                </figure>
            }
        </div>
        <div className="media-content">
            {
                props.itemTypeAndTierDisplayName &&
                (
                    <p className="subtitle is-3">
                        {props.itemTypeAndTierDisplayName}
                    </p>
                )
            }
            {props.description}
        </div>
    </div>
)
}