import React from "react";
import { NavLink } from "react-router-dom";
import BungieApi from '../helpers/BungieApi';
import logoImg from "../images/logo.png"

function Header() {
    const bungieApi = new BungieApi();
    const manifestData = bungieApi.getManifestData();
    const [isActive, setisActive] = React.useState(false);
    return (
        <nav className="navbar hasshadow is-spaced is-dark is-radiusless" role="navigation" aria-label="main navigation">
            <div className="navbar-brand">
                <NavLink exact className="navbar-item is-radiusless" to="/">
                    <img src={logoImg} alt="Logo"/>

                </NavLink>
                <NavLink exact className="navbar-item" to="/">
                    Home
                </NavLink>
                <a role="button" className={`navbar-burger burger ${isActive ? "is-active" : ""}`} aria-label="menu" aria-expanded="false"
                   onClick={() => {
                       setisActive(!isActive);
                   }}
                   data-target="mainNav">
                    <span aria-hidden="true"></span>
                    <span aria-hidden="true"></span>
                    <span aria-hidden="true"></span>
                </a>
            </div>

            <div id="mainNav" className={`is-radiusless navbar-menu ${isActive ? "is-active" : ""}`}>
                <div className="navbar-start">
                    <div className="navbar-item has-dropdown is-hoverable">
                        <a className="navbar-link">
                            Destiny DB
                        </a>

                        <div className="navbar-dropdown">
                            {
                                manifestData.map( (data,index) => (
                                    <NavLink key={data.path} className="navbar-item" to={data.path}>
                                        {data.title}
                                    </NavLink>
                                ))
                            }
                        </div>
                    </div>
                </div>
                <div className="navbar-end">

                </div>
            </div>
        </nav>
    );
}
export default Header;