import React from 'react'
import BungieApi from "../helpers/BungieApi";
import EntryItem from "./EntryItem";
import PageTitle from './PageTitle';

export default function Socketcategory(props) {
    const bungieApi = new BungieApi();

    const newProps = {...props, dataPath: "DestinySocketCategoryDefinition"};
    return (
        <EntryItem {...newProps}>
            {data => (
                <div>
                    <PageTitle text={data.originalDisplayProperties.name}/>
                    <p>
                        {data.originalDisplayProperties.description}
                    </p>
                </div>
            )
            }
        </EntryItem>);
}