import React from 'react'
import BungieApi from "../helpers/BungieApi";
import EntryItem from "./EntryItem";
import PageTitle from './PageTitle';

export default function InventoryItem(props) {
    const bungieApi = new BungieApi();

    const newProps = {...props, dataPath: "DestinyInventoryItemDefinition"};
    return (
        <EntryItem {...newProps}>
            {data => (
                <div>
                    <PageTitle text={data.displayProperties.name}/>
                    <div className="media section">
                        <div className="media-left">
                            {
                                data.displayProperties.hasIcon &&
                                <figure className={"icon-image image is-96x96"}>
                                    <img src={bungieApi.getBungieImagePath(data.displayProperties.icon)} alt="Item image"/>
                                </figure>
                            }
                        </div>
                        <div className="media-content">
                            {
                                data.itemTypeAndTierDisplayName &&
                                (
                                  <p className="subtitle is-3">
                                      {data.itemTypeAndTierDisplayName}
                                  </p>
                                )
                            }
                            {data.displayProperties.description}
                        </div>
                    </div>
                    {
                        data.screenshot && (
                        <div className="image section">
                            <img src={bungieApi.getBungieImagePath(data.screenshot)} alt="Screenshot"/>
                        </div>
                        )
                    }
                </div>
            )
            }
        </EntryItem>);
}