import React from 'react'
import BungieApi from "../helpers/BungieApi";
import EntryItem from "./EntryItem";
import PageTitle from './PageTitle';

export default function NodeStepSummary(props) {
    const bungieApi = new BungieApi();

    const newProps = {...props, dataPath: "DestinyNodeStepSummaryDefinition"};
    return (
        <EntryItem {...newProps}>
            {data => (
                <div>
                    <PageTitle text={data.displayProperties.name}/>
                    <p>
                        {data.displayProperties.description}
                    </p>
                    <div className={"icon-image"}>
                      <img src={bungieApi.getBungieImagePath(data.displayProperties.icon)}/>
                    </div>
                    <p>
                        {data.sourceString}
                    </p>
                </div>
            )
            }
        </EntryItem>);
}