export default class BungieApi {
    constructor() {
        const bungieUrl = "//www.bungie.net";
        const baseUrl = "//www.bungie.net/Platform";
        const dataBaseUrl = "//lordnuker.github.io/data/games/d2";
        const defaultTimeout = 10000;

        this.getApiKey = function()
        {
            return "f7bb7244639a4fee85f06abd76d4c019";
        };


        this.makeHtml = function(text) {
            return {__html: text}
        };

        this.getBungieImagePath = function(imagePath) {
            return bungieUrl + imagePath;
        };


        this.formatPlainText = function(text) {
            return "<p>" + text.split("\n").join("</p><p>") + "</p>";
        };

        this.getDataUrl = function(folder) {
            return dataBaseUrl + "/" + folder;
        };

        const data = [];

        this.getManifestData = function() {
            if( data.length === 0 ) {
                const createManifestEntry = function(title, folder, path) {
                    return {
                        title: title,
                        folder: folder,
                        path: path,
                        dataUrl: dataBaseUrl + "/" + folder + "/index.json",
                        detailPage: path + '/:id'
                    }
                };


                data.push(createManifestEntry("Lore", "DestinyLoreDefinition", "/db/lore"));
                data.push(createManifestEntry("Destination", "DestinyDestinationDefinition", "/db/destination"));
                data.push(createManifestEntry("Collectible", "DestinyCollectibleDefinition", "/db/collectible"));
                data.push(createManifestEntry("Inventory Item", "DestinyInventoryItemDefinition", "/db/inventoryitem"));
                data.push(createManifestEntry("Activity", "DestinyActivityDefinition", "/db/activity"));
                data.push(createManifestEntry("Activity Modifier", "DestinyActivityModifierDefinition", "/db/activitymodifier"));
                data.push(createManifestEntry("Activity Mode", "DestinyActivityModeDefinition", "/db/activitymode"));
                data.push(createManifestEntry("Socket Category", "DestinySocketCategoryDefinition", "/db/socketcategory"));
                data.push(createManifestEntry("Node Step Summary", "DestinyNodeStepSummaryDefinition", "/db/nodestepsummary"));
            }
            return data;
        }

        this.getManifestEntry = function(folder) {
            const manifestData = this.getManifestData();
            const filtered = manifestData.filter( f => f.folder === folder );
            return filtered.length > 0 ? filtered[0] : null;
        }
    }
}