import React from 'react'
import BungieApi from "../helpers/BungieApi";
import EntryItem from "./EntryItem";
import PageTitle from './PageTitle';
import MediaCard from "./MediaCard";

export default function Collectible(props) {
    const bungieApi = new BungieApi();

    const newProps = {...props, dataPath: "DestinyCollectibleDefinition"};
    return (
        <EntryItem {...newProps}>
            {data => (
                <div>
                    <PageTitle text={data.displayProperties.name}/>
                    <MediaCard {...data.displayProperties}/>
                    <div className={"section content"}>
                        <p>
                            {data.sourceString}
                        </p>
                    </div>
                </div>
            )
            }
        </EntryItem>);
}