import React from 'react'
import BungieApi from "../helpers/BungieApi";
import EntryItem from "./EntryItem";
import PageTitle from './PageTitle';

export default function Destination(props) {
    const bungieApi = new BungieApi();

    const newProps = {...props, dataPath: "DestinyDestinationDefinition"};
    return (
        <EntryItem {...newProps}>
            {data => (
                <div>
                    <PageTitle text={data.displayProperties.name}/>
                    <div className="section content">
                        {data.displayProperties.description}
                    </div>

                    <div className="section">
                        <div className="tile is-ancestor">
                            <div className="tile is-vertical">
                                {data.bubbles && data.bubbles.length > 0 && (
                                    <div>
                                    <div className="subtitle is-4">Areas</div>
                                        {data.bubbles.map( bubble => (<div key={bubble.hash}>{bubble.displayProperties.name}</div>) )}
                                    </div>
                                )}
                            </div>
                        </div>
                    </div>


                </div>
            )
            }
        </EntryItem>);
}