import React, { useState, useEffect } from 'react'
import axios from 'axios'
import {Link, Route} from "react-router-dom";
import BungieApi from '../helpers/BungieApi';

export default function DataNavigator(props) {
    const bungieApi = new BungieApi();

    const defaultEntryList = {
        list: {},
        loading: true,
    };

    const [entryList, setEntryList] = useState(defaultEntryList);

    useEffect(() => {
        const getEntryList = async () => {

            const {data} = await axios(bungieApi.getDataUrl(props.list));

            const sortedList = data.sort( (a, b) => a.index - b.index );
            // Update state
            setEntryList({list: sortedList, loading: false})
        };

        getEntryList();
    }, []);


    return  entryList.loading ? (
        <progress className="progress is-small is-primary" max="100"/>
    ) : (
        <div>
            <Route key={props.baseNav} path={props.baseNav}>
                <div className="column is-hidden-mobile is-fullheight has-background-grey-light">
                    <nav className="panel is-dark is-centered is-radiusless">
                        <p className="panel-heading is-radiusless">{props.title}</p>
                        <div className="dl-scrollable-nav">
                            {
                                entryList.list.map( (e)=>
                                    <Link className="panel-block" key={e.name} to={props.baseNav + "/" + e.name}>{e.title || e.name}</Link>)
                            }
                        </div>
                    </nav>
                </div>
            </Route>
            <Route key={props.baseNav + '-mobile'} path={props.baseNav} exact>
                <div className="column is-hidden-desktop">
                    <nav className="panel is-dark is-centered is-radiusless">
                        <p className="panel-heading is-radiusless">{props.title}</p>
                        <div className="panel-block tile is-ancestor">
                            {
                                entryList.list.map( (e)=>
                                    <div className="tile" key={e.name + '-tile'}>
                                        <Link key={e.name + '-mobile'} to={props.baseNav + "/" + e.name}>{e.title || e.name}</Link>
                                    </div>)
                            }
                        </div>
                    </nav>
                </div>
            </Route>
        </div>
    );
}