import React from 'react'
import BungieApi from "../helpers/BungieApi";
import EntryItem from "./EntryItem";
import PageTitle from './PageTitle';
import MediaCard from "./MediaCard";
import ActivityModeSummary from "./ActivityModeSummary";

export default function ActivityItem(props) {
    const bungieApi = new BungieApi();

    const newProps = {...props, dataPath: "DestinyActivityDefinition"};
    return (
        <EntryItem {...newProps}>
            {data => (
                <div>
                    <PageTitle text={data.displayProperties.name}/>
                    <MediaCard {...data.displayProperties}/>
                    <div className="section">
                        <div className="tile is-ancestor is-radiusless">
                        {
                            data.activityModeHashes && data.activityModeHashes.length > 0 &&
                            (
                                <div className="tile is-vertical">
                                    <div className="subtitle is-4">Activity Modes</div>
                                    {
                                        data.activityModeHashes.map( hash => <ActivityModeSummary key={hash} hash={hash}/> )
                                    }
                                </div>
                            )
                        }
                        </div>
                    </div>
                    <div className="section image">
                      <img src={bungieApi.getBungieImagePath(data.pgcrImage)}/>
                    </div>
                </div>
            )
            }
        </EntryItem>);
}